.imagecont{
    background: url('https://i.ibb.co/V2MqhFR/food-wallpaper.png');
    border-style: solid;
    border-color: white;
    /*border-width: 30px;*/
    background-repeat: no-repeat;
    background-size: cover;
    margin-top: 0px;
    width: 95%;
    display: block;
    margin: auto;
   
}

#logo{
    text-align: center;
    color: red;
    font-size: 75px;
    font-weight: bold;
    background: white;
    width: 120px;
    height: 120px;
    margin: auto;
    border-radius: 50%;
    margin-top: 80px;
    margin-bottom: 80px;
}
.heading{
    text-align: center;
    color: white;
    font-size: 36px;
    font-weight: 100;
}
.locationselector{
